import './detail.css'
import './fonts/iconfont.js'
import checkClient from './check_client'
import getParams from './get_params.js'
let labels = document.getElementById('labels'),
  		alipay_label = document.getElementById('alipay_label'),
  		alipay = document.getElementById('alipay'),
  		wechatpay_label = document.getElementById('wechatpay_label'),
  		wechatpay = document.getElementById('wechatpay'),
  		client = checkClient(),
			params_obj = getParams()
if (client === 'wechatpay') {
	labels.removeChild(alipay_label)
	wechatpay.checked = true
} else if (client === 'alipay') {
	labels.removeChild(wechatpay_label)
	alipay.checked = true
}
function ensurePay(){
	let pay_eles = document.getElementsByName('pay')
	let checked_type = 'alipay', data_type = '支付宝支付'
	let arr = Array.from(pay_eles)
	for(let i = 0; i < arr.length; i++){
		if(arr[i].checked){
			checked_type = arr[i].id
			data_type = arr[i].dataset.type || data_type
		}
	}
	let encryption = encodeURIComponent(data_type)
	let p1 = encryption.slice(0, 6), p3 = encryption.slice(6)
	location.href = '/dist/code.html?p1=' + p1 + '&p2=' + params_obj.id + '&p3=' + p3
}

window.ensurePay = ensurePay
document.addEventListener('DOMContentLoaded', () => {
    let goods_name = document.getElementById('goods_name'), //商品名称
        txcurrcd = document.getElementById('txcurrcd'), //币种
        txamt = document.getElementById('txamt')
		let post_params = {
			params: {
				id: params_obj.id
			}
		}
    axios.get('/api/v1/order/info', post_params).then(setData).catch(res => {
        axios.get('http://qpay.mengant.cn/api/v1/order/info', post_params).then(setData)
    })
    function setData(res){
        let data = res.data
        goods_name.innerHTML = data.goods_name
        txcurrcd.innerHTML = data.txcurrcd
        txamt.innerHTML = data.txamt
    }
})